import React, { useEffect, useState } from 'react';
import NullImg from '../../img/.png';
import Profile from '../../img/.png';
import { useParams } from 'react-router-dom';
import './DetailBoard.scss';
import TOGETHER from '../../img/.png';
import LEVEL from '../../img/.png';
import TIME from '../../img/.png';
import KAKAO from '../../img/.png';
import FACEBOOK from '../../img/.png';
import TWITER from '../../img/.png';
import { ALLUSER, COMMENTS, REVIEW, sendGet, strDivide, URL } from '../../util/util.tsx';
import ReviewComments from '../../components/reviewcomments/ReviewComments';
import { useSelector } from 'react-redux';
import RepleBox from '../../components/replebox/RepleBox';
import { Button } from 'react-bootstrap';
interface InputList {
    USER_NM: string;
    RCP_TTL: string;
    CK_INFO: string;
    CK_INBUN_NM: string;
    CK_TIME_NM: string;
    CK_LEVEL_NM: string;
    CK_INPUT_CON: {
        inputName: string[];
        inputCount: string[];
    };
}

const Detailboard: React.FC = () => {
    const [inputList, setInputList] = useState<InputList>({
        USER_NM: '',
        RCP_TTL: '',
        CK_INFO: '',
        CK_INBUN_NM: '',
        CK_TIME_NM: '',
        CK_LEVEL_NM: '',
        CK_INPUT_CON: {
            inputName: [],
            inputCount: []
        }
    });

    const [reviewList, setReviewList] = useState<any[]>([]);
    const [comdList, setComdList] = useState<any[]>([]);
    const { pathNo } = useParams<{ pathNo: string }>();

    useEffect(() => {
        sendGet(`${URL}/detail_board?sno=${pathNo}`, initData, null);
        sendGet(`${URL}/getReview?rcp_sno=${pathNo}`, setReviewList);
        sendGet(`${URL}/getComment?rcp_sno=${pathNo}`, setComdList);
    }, [pathNo]);

    useEffect(() => {
        console.log(reviewList);
    }, [reviewList]);

    const Name = useSelector((state: any) => state.user);
    
    const [isCheck, setIsCheck] = useState<boolean>(false);
    
    useEffect(() => {
        if (inputList.USER_NM === Name?.displayName) {
            setIsCheck(true);
        }
    }, [inputList, Name]);

    function initData(data: any) {
        console.log(data)
        const result: InputList = { ...data[0], CK_INPUT_CON: strDivide(data[0].CK_INPUT_CON) };
        setInputList(result);
    }

    return (
        <div >
            
                <div className='detail_container_1 '>
                    <div className='detail_food_imgBox mx-auto'>
                        <img className='detail_food_img ' src={NullImg} alt=" " />
                        <img className='detail_profile_img cursor' src={Profile} alt=" " />
                        <p className='detail_profile_name'>{inputList.USER_NM}</p>
                    </div>
                    <h2>{inputList.RCP_TTL}</h2>
                    <p>{inputList.CK_INFO}</p>

                    <div className='divFlex'>
                        <div className='divFlexRow'>
                            <img className='sp' src={TOGETHER} alt="" />
                            <span>{inputList.CK_INBUN_NM}</span>
                        </div>
                        <div className='divFlexRow'>
                            <img className='sp' src={TIME} alt="" />
                            <span>{inputList.CK_TIME_NM}</span>
                        </div>
                        <div className='divFlexRow'>
                            <img className='sp' src={LEVEL} alt="" />
                            <span>{inputList.CK_LEVEL_NM}</span>
                        </div>
                    </div>
                </div>
                <div className='grayLine'></div>
                <div className='divFlex'>
                    <img className='sp circle cursor' src="https://recipe1.ezmember.co.kr/img/mobile/icon_url_copy.gif" alt="URL" />
                    <img className='sp cursor' src={KAKAO} alt="" />
                    <img className='sp cursor' src={FACEBOOK} alt="" />
                    <img className='sp cursor' src={TWITER} alt="" />
                </div>
                <div className='grayLine'></div>
                <div className='divFlex'>
                    <img className='cursor' src="https://recipe1.ezmember.co.kr/img/btn2_id.png" alt="" />
                    <img className='cursor' src="https://recipe1.ezmember.co.kr/img/btn2_note.png" alt="" />
                    <img className='cursor' src="https://recipe1.ezmember.co.kr/img/btn2_error.png" alt="" />
                    <img className='cursor' src="https://recipe1.ezmember.co.kr/img/btn2_print.png" alt="" />
                </div>
                <div className='grayLine'></div>

            <div className='container'>
                <h2></h2>
                <p>[]</p>
                <div className='middleWidth flex_justify flex_wrap'>
                    {inputList.CK_INPUT_CON.inputName.map((item, idx) => (
                        <div key={idx} className='divFlex_between'>
                            <span>{item}</span>
                            <span>{inputList.CK_INPUT_CON.inputCount[idx]}</span>
                            <span></span>
                        </div>
                    ))}
                </div>
                <div className='grayLine'></div>
                <h2></h2>
                <div className='grayLine'></div>
                <h2></h2>
                <div className='grayLine'></div>
                <h2> </h2>
                <div className='flex_wrap flex_left'>
                    <img className='pp' src={Profile} alt="" />
                    <span>{inputList.USER_NM}</span>
                </div>
                <div className='grayLine'></div>
            </div>


            <div className='container'>
                <h2>{REVIEW} <span>{reviewList.length}</span></h2>
                {reviewList.length > 0 && reviewList.map((item, index) => (
                    <div key={index} className='flex_wrap flex_left border_bottom fullWidth'>
                        <ReviewComments 
                            type={REVIEW} 
                            data={{
                                USER_NM: item.USER_NM,
                                REVIEW_REG_DT: item.REVIEW_REG_DT,
                                REVIEW_SCORE: item.REVIEW_SCORE,
                                value: item.REVIEW,
                                show: isCheck
                            }} 
                        />
                    </div>
                ))}
            </div>
            <div className='grayLine'></div>
            <div className='container'>
                <h2>{COMMENTS} <span>{comdList.length}</span></h2>
                {comdList.length > 0 && comdList.map((item, index) => (
                    <div key={index} className='flex_wrap flex_left border_bottom fullWidth'>
                        <ReviewComments 
                            type={COMMENTS} 
                            data={{
                                USER_NM: item.USER_NM,
                                REVIEW_REG_DT: item.COMMENTS_REG_DT,
                                value: item.COMMENTS
                            }} 
                        />
                    </div>
                ))}
            </div>

            {!isCheck && <RepleBox type={ALLUSER} pageNumber={String(pathNo)}/>}
        </div>
    );
};

export default Detailboard;
