export const LOCALEMAIL : string = "EMAIL"

export function setScore(idx : any){
    let stars = []
    
    for(let i = 0; i<5; i++)
    {
        if(i<idx)
        {
            stars.push(<img key={i} className='star' src={star} alt=''/>)
        }
        else{
            stars.push(<img key={i} className='star' src={emptyStar} alt=''/>)
        }
    }
    return stars;
}

export function sendGet(url : string, func : any = null , data : any =null){
    
    axios
        .get(url, {
            data : data,
        })
        .then(res => {
            
            if(func != null)
            {
                func(res.data)
            }
        })
}



