export interface Category {
    CK_KIND_NM: string;
    CK_STA_NM: string;
    CK_INPUT_NM: string;
    CK_ACT_NM: string;
}

export interface userInfoType {
    nick : string,
    email : string
}


interface SetUserInfoAction {
    type: typeof SETUSERINFO;
    //user : any; //  
    user: userInfoType;
}

interface AddCategoryAction {
    type: typeof ADDCATEGORY;
    key: number;
    // data : any;
    data: string; //   
}

interface ClearCategoryAction {
    type: typeof CLEARCATEGORY;
}

interface AddCategoryTagAction {
    type: typeof ADDCATEGORYTAG;
    // list : any[];
    list: categoryTagType[]; //    
}

interface plus {
    type : typeof PLUS;
    num : number;
}

interface minus {
    type : typeof MINUS;
    num : number;
}



export interface categoryTagType {
    List: string;
    idx: number;
}

export type CategoryActionTypes =
    | SetUserInfoAction
    | AddCategoryAction
    | ClearCategoryAction
    | AddCategoryTagAction
    | plus
    | minus;




