import React, { ChangeEvent } from 'react'
import { Dropdown } from 'react-bootstrap';
import './InsertInfo.scss'

// foodType     
//   , string  
interface foodType {
    kind: string[];  //  
    state: string[]; //  
    input: string[]; //  
    act: string[];   //  
}

interface DropCateProps {
    categories: {
        kind: string;
        state: string;
        input: string;
        act: string;
    };
    handleSelectC: (key: keyof foodType) => (e: ChangeEvent<HTMLSelectElement>) => void;
    foodCateDic: foodType;
}

const DropCate: React.FC<DropCateProps> = ({ categories, handleSelectC, foodCateDic }) => {

    return (
        <div>
            <div className="d-flex flex-row gap-2">
                {Object.keys(foodCateDic).map((key, idx) => {
                    const typeKey = key as keyof foodType;

                    return (
                        <div>
                            <Dropdown key={idx} className="dropdown-main"  >
                                <Dropdown.Toggle variant='dark' id={`dropdown-${typeKey}`} className='dropdown-toggle'>
                                    {categories[typeKey] || (typeKey === "kind" ? "" :
                                        typeKey === "state" ? "" :
                                            typeKey === "input" ? "" : "")}
                                </Dropdown.Toggle>

                                <Dropdown.Menu className='dropdown-menu'>
                                    {/*      .*/}
                                    {/* <Dropdown.Item as="button" disabled>
                                        {(typeKey === "kind" ? "" :
                                            typeKey === "state" ? "" :
                                                typeKey === "act" ? "" : "")}
                                    </Dropdown.Item> */}

                                    {foodCateDic[typeKey]
                                        .filter(item => item !== "")
                                        .map((item, index) => (
                                            <Dropdown.Item
                                                key={index}
                                                as="button"
                                                className='dropdown-item'
                                                onClick={() => handleSelectC(typeKey)({ target: { value: item } } as ChangeEvent<HTMLSelectElement>)}
                                            >
                                                {item}
                                            </Dropdown.Item>
                                        ))}
                                </Dropdown.Menu>
                            </Dropdown>
                        </div>
                    )
                })}
            </div>
        </div>
    );
};

