import React, { useEffect, useState } from 'react';
import './InsertRecipe.scss';
import { recipeCategory, recipeInfo } from 'util/util';
// import AddInput from 'components/inputInfo/AddInput';
import AddBundle from 'components/insertInfo/AddBundle';
import AddStep from 'components/insertInfo/AddStep';
import DropCate from 'components/insertInfo/DropCate';
import DropCkInfo from 'components/insertInfo/DropCkInfo';
import { Button, Form } from 'react-bootstrap';
// import { useDispatch, useSelector } from 'react-redux';
// import { State } from '../../redux/reducer/index';
// import { minus, Plus } from  '../../redux/actions/index';


interface inputType {
  Main: string;
  Quantity: string;
  Unit: string;
  Note: string;
}
export interface bundleType {
  inputTitle: string;
  InputValue: inputType[];
}

const InsertRecipe = () => {

  //  
  const [rcpTtl, setRcpTtl] = useState<string>("");

  const handelRcpTtl = (e: React.ChangeEvent<HTMLInputElement>) => {
    setRcpTtl(e.target.value);
  }

  //  
  const [cookInfo, setCookInfo] = useState<string>("");

  const handelCookInfo = (e: React.ChangeEvent<HTMLInputElement>) => {
    setCookInfo(e.target.value);
  }

  useEffect(() => {
    console.log(rcpTtl);
    console.log(cookInfo);
  }, [cookInfo]);

  //   
  const [categories, setCategories] = useState({
    kind: "",
    state: "",
    input: "",
    act: "",
  });

  const handleSelectC = (key: keyof typeof categories) => (e: React.ChangeEvent<HTMLSelectElement>) => {
    setCategories({
      ...categories,
      [key]: e.target.value,
    });
  };

  // foodType  
  interface foodType {
    kind: string[];
    state: string[];
    input: string[];
    act: string[];
  }

  const foodCateDic: foodType = {
    kind: recipeCategory("type") || [],
    state: recipeCategory("state") || [],
    input: recipeCategory("ingredient") || [],
    act: recipeCategory("method") || [],
  };


  //    
  const [ckInfo, setCkInfo] = useState({
    inbun: "",
    time: "",
    level: "",
  });

  const handleSelectI = (key: keyof typeof ckInfo) => (e: React.ChangeEvent<HTMLSelectElement>) => {
    setCkInfo({
      ...ckInfo,
      [key]: e.target.value,
    });
  };

  interface infoType {
    inbun: string[];
    time: string[];
    level: string[];
  }

  const foodInfoDic: infoType = {
    inbun: recipeInfo("inbun") || [],
    time: recipeInfo("time") || [],
    level: recipeInfo("level") || [],
  };



  //    
  const [bundles, setBundles] = useState([
    { inputTitle: "", InputValue: [{ Main: "", Quantity: "", Unit: "", Note: "" }, { Main: "", Quantity: "", Unit: "", Note: "" }] }
  ]);

  // handleBundleChange : field "Main" | "Quantity" | "Unit" | "Note" 
  const handleBundleChange = (
    index: number,
    field: "Main" | "Quantity" | "Unit" | "Note",
    value: string,
    inputIndex: number
  ) => {
    let changeList = [...bundles];
    //      type
    if (field === "Main") {
      // index    
      // idx  InputValue 
      // updatedBundles[index].InputValue[idx].Main = e.target.value;
      // setBundles(updatedBundles[index])
      changeList[index].InputValue[inputIndex].Main = value
    }
    else if (field === "Quantity") {
      changeList[index].InputValue[inputIndex].Quantity = value
    }
    else if (field === "Unit") {
      changeList[index].InputValue[inputIndex].Unit = value
    }
    else if (field === "Note") {
      changeList[index].InputValue[inputIndex].Note = value
    }

    // if (inputIndex === -1) {
    //   updatedBundles[index].inputTitle = value;  // inputTitle 
    // } else {
    //   updatedBundles[index].InputValue[inputIndex][field] = value; //   
    // }

    setBundles(changeList);
  };

  const addBundle = () => {
    setBundles([
      ...bundles,
      { inputTitle: "", InputValue: [{ Main: "", Quantity: "", Unit: "", Note: "" }, { Main: "", Quantity: "", Unit: "", Note: "" }] }
    ]);
  };

  const removeBundle = () => {
    if (bundles.length > 1) {
      setBundles(bundles.slice(0, -1)); //  bundle 
    }
  };

  useEffect(() => {
    console.log("bundle : ", bundles);
  }, [bundles]);

  return (
    <div>
      <div className='insertHeader'> </div>

      <span className='first-item-title'> </span>
      <Form.Control
        type="text"
        name="RCP_TTL"
        placeholder=")  "
        onChange={handelRcpTtl}
        className='form-rcpttl' />
      <br />

      <span className='first-item-title'> </span>
      <Form.Control
        type="text"
        name="CK_INFO"
        placeholder="   "
        onChange={handelCookInfo}
        className='form-ckinfo' />
      <br />

      <div className='d-flex align-items-center'>
        <span className='first-item-title'></span>
        <DropCate
          categories={categories} //state 
          handleSelectC={handleSelectC} //  
          foodCateDic={foodCateDic} //   
        />
      </div>

      <p className='gray_info first-item-gray'>   ,      .</p>

      <div className='d-flex align-items-center'>
        <span className='first-item-title'> </span>
        <DropCkInfo
          ckInfo={ckInfo}
          handleSelectI={handleSelectI}
          foodInfoDic={foodInfoDic}
        />
      </div>

      <hr className='gray-boldline' />

      <div className='second-item'>
        <span className='second-item-title'> </span>
        <Button variant='outline-info' size='sm' className='second-btn'>+   </Button>
        <span className='gray_info'>       ","       .</span>
        <p className='gray_info second-item-gray'>       .</p>

        {bundles.map((_, index) => (
          <AddBundle
            key={index}
            index={index}
            bundle={bundles}
            onBundleChange={handleBundleChange}  //      
            setFunc={setBundles}
          />
        ))}

        <div className='BundelBtn'>
          <Button variant='outline-info' onClick={removeBundle} className='cursor removebundle' disabled={bundles.length <= 1}>-    </Button>
          <hr />
          <p className='red_info'> , , , , , ,     .</p>
          <Button variant='outline-secondary' onClick={addBundle} className='cursor addbundle'>+    </Button>
        </div>
      </div>

      <hr className='gray-boldline' />
    </div>
  );
};

export default InsertRecipe;
