import React, { useState } from 'react';
import { checkError, signupEmail } from '../../util/auth/firebase';
import { updateProfile } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import { DEVALOPTYPE, inputRegexs, sendPost, URL } from '../../util/util.tsx';
import warnning from '../../img/.png'


interface inputValueType {
    emailId: string, //    
    emailAddress: string, //    
    validEmail: boolean, //   
    pw: string //   
    validPw: boolean, //   
    pwCheck: string, //    
    correctPwCheck: boolean,  //    
    nickName: string, //    
    validNickName: boolean, //    
    nonNickNameDuplication: boolean //   
}

//  
const CreateUser: React.FC = () => {
    const [inputValue, setInputValue] = useState<inputValueType>({
        emailId: "", //    
        emailAddress: "naver.com", //    
        validEmail: DEVALOPTYPE === 1 ? false : true, //    
        pw: "", //   
        validPw: DEVALOPTYPE === 1 ? false : true, //   
        pwCheck: "", //    
        correctPwCheck: DEVALOPTYPE === 1 ? false : true,  //    
        nickName: "", //    
        validNickName: DEVALOPTYPE === 1 ? false : true, //    
        nonNickNameDuplication: true //   
    })

    //       
    const [alertMessage, setAlertMessage] = useState({
        email: "    .",
        pw: "    .",
        pwCheck: "  .",
        nickname: "    .",
    });

    //      
    const [passMessage, setPassMessage] = useState({
        email: "    .",
        pw: "    .",
        pwCheck: "  .",
        nickname: "    .",
    });

    const [pwWarnning, setPwWarnning] = useState<boolean>(false)

    const submitRequirements: boolean =
        Boolean(inputValue.emailId) &&
        Boolean(inputValue.emailAddress) &&
        inputValue.validEmail &&
        Boolean(inputValue.pw) &&
        Boolean(inputValue.validPw) &&
        Boolean(inputValue.pwCheck) &&
        inputValue.correctPwCheck &&
        Boolean(inputValue.nickName) &&
        inputValue.validNickName &&
        inputValue.nonNickNameDuplication;


    const nav = useNavigate();

    function msgBox(e : React.MouseEvent<HTMLImageElement>) : void {
        console.log(e)
        

    }

    const handleSignup = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        try {
            const userCredential = await signupEmail(inputValue.emailId + "@" + inputValue.emailAddress, inputValue.pw);
            const user = userCredential.user;
            console.log(inputValue)
            await sendPost(URL + "/userInfo", null, { email: inputValue.emailId + "@" + inputValue.emailAddress, nick: inputValue.nickName });

            // displayName 
            await updateProfile(user, {
                displayName: inputValue.nickName
            });
            nav(-1);
        } catch (error :any) {
            checkError(error.code)
        }
    };

    return (
        <div className='Logininner'>
            <div className='inner login'>
                <h2></h2>
                <form onSubmit={handleSignup} >
                    <input
                        type="text"
                        onChange={e => {
                            
                            //    
                            let check = false;
                            if(DEVALOPTYPE === 0)
                            {
                                check = true;
                            }
                            else if(DEVALOPTYPE === 1)
                            {
                                check = inputRegexs.idRegex.test(e.target.value);
                            }

                            setInputValue(
                                {
                                    ...inputValue,
                                    emailId: e.target.value,
                                    validEmail: check
                                }
                            )
                        }
                        }
                        placeholder=''
                        name="emailId"

                    /> &nbsp; @ &nbsp;
                    <select name="emailAddress" onChange={e => {
                        setInputValue(
                            {
                                ...inputValue,
                                emailAddress: e.target.value
                            }
                        )
                    }
                    }
                    
                    >
                        <option value="naver.com" selected>naver.com</option>
                        <option value="kakao.com">kakao.com</option>
                        <option value="gmail.com">gmail.com</option>
                    </select> {inputValue.validEmail ? passMessage.email: alertMessage.email} 
                    <br />
                    <input
                        type="password"
                        name='pw'
                        onChange={e => {
                            //   
                            let check = false;
                            if(DEVALOPTYPE === 0)
                            {
                                check = true;
                            }
                            else if(DEVALOPTYPE === 1)
                            {
                                check = inputRegexs.pwRegex.test(e.target.value);
                            }
                            

                            setInputValue(
                                {
                                    ...inputValue,
                                    pw: e.target.value,
                                    validPw: check
                                }
                            )
                        }
                        }
                        placeholder=''
                        // required
                    />  
                    <img className='star' src={warnning} alt="" onMouseEnter={(e)=> msgBox(e)}/>
                    
                    &nbsp; {inputValue.validPw ? passMessage.pw: alertMessage.pw}
                    <br/>
                    <input
                        type="password"
                        name='pwCheck'
                        onChange={e => {
                            //     
                            let check = false;
                            if(DEVALOPTYPE === 0)
                            {
                                check = true;
                            }
                            else if(DEVALOPTYPE === 1)
                            {
                                if (inputValue.pw === e.target.value)
                                    {
                                        check = true;
                                    }
                            }
                            
                            



                            setInputValue(
                                {
                                    ...inputValue,
                                    pwCheck: e.target.value,
                                    correctPwCheck : check
                                }
                            )
                        }
                        }
                        placeholder=' '
                        // required
                    /> 
                    {/* { 
                        pwWarnning && <div style={{position: "fixed", top : e.pageY, left: e.pageX, backgroundColor : "brown"}}>
                        112313
                                </div>
                    }         */}
                    
                    &nbsp; {inputValue.correctPwCheck ? passMessage.pwCheck: alertMessage.pwCheck}
                    <br />
                    <input
                        type="text"
                        name='nick'
                        onChange={e => {
                            //   
                            let check = false;
                            if(DEVALOPTYPE === 0)
                            {
                                check = true;
                            }
                            else if(DEVALOPTYPE === 1)
                            {
                                check = inputRegexs.nicknameRegex.test(e.target.value);
                            }
                            
                            setInputValue(
                                {
                                    ...inputValue,
                                    nickName: e.target.value,
                                    validNickName: check
                                }
                            )
                        }
                        }
                        placeholder=''
                        // required
                    />  
                    &nbsp; {inputValue.nickName ? passMessage.nickname: alertMessage.nickname}
                    <br />
                    <div style={{ display: "flex", justifyContent: "space-around" }}>
                        <button
                            type="submit"
                            className='blueBtn oneBtn bHover'
                            disabled={!submitRequirements}
                            
                        >
                            
                        </button>
                        
                    </div>
                </form>
                <button onClick={()=>{
                    console.log(inputValue)
                }}>
                    
                </button>
            </div>
        </div>
    );
};

export default CreateUser;
