// redux/reducer/index.ts

import * as types from '../actions/ActionTypes';


//   
export interface State {
    isLogin: boolean;
    backMode: boolean;
    user: types.userInfoType; //   
    category: types.Category;
    categoryTag: types.categoryTagType[]; //     
    num : number;
}

//   
const initialState: State = {
    isLogin: false,
    backMode: false,
    user: {
        nick : "",
        email : "",
    },
    category: {
        CK_KIND_NM: '',
        CK_STA_NM: '',
        CK_INPUT_NM: '',
        CK_ACT_NM: '',
    },
    categoryTag: [],
    num : 100,

        
};

const counterReducer = (state: State = initialState, action: types.CategoryActionTypes) => {
    switch (action.type) {
        case types.SETUSERINFO:
            return {
                ...state,
                isLogin: !state.isLogin,
                user: action.user, // user null  
            };
        case types.ADDCATEGORY:
            let dic = { ...state.category };
            switch (action.key) {
                case 1:
                    dic.CK_KIND_NM = action.data;
                    break;
                case 2:
                    dic.CK_STA_NM = action.data;
                    break;
                case 3:
                    dic.CK_INPUT_NM = action.data;
                    break;
                case 4:
                    dic.CK_ACT_NM = action.data;
                    break;
                default:
                    break; //  
            }

            return {
                ...state,
                category: dic,
            };
        case types.CLEARCATEGORY:
            console.log("clear");
            return {
                ...state,
                category: {
                    CK_KIND_NM: "",
                    CK_STA_NM: "",
                    CK_INPUT_NM: "",
                    CK_ACT_NM: "",
                },
            };

        case types.ADDCATEGORYTAG:
            console.log(action.list);
            return {
                ...state,
                categoryTag: [...action.list], //    
            };
        case types.PLUS:
            console.log(action)
            let number = state.num + action.num;
            return {
                ...state,
                num : number
            }
        case types.MINUS:
            return {
                ...state,
                num : state.num - action.num
            }
        default:
            return state;
    }
};

export default counterReducer;

