import { useEffect } from "react";

//   
export const usePreventRefresh = () => {
    const preventClose = (e: any) => {
      e.preventDefault();
      e.returnValue = '';
    };
  
    //       
    useEffect(() => {
      (() => {
        window.addEventListener('beforeunload', preventClose);
      })();
  
      return () => {
        window.removeEventListener('beforeunload', preventClose);
      };
    });
  };