import React, { useEffect } from 'react';
import SearchOk from '../../img/.png';
import Profile from '../../img/.png';
import MenuBtn from '../../img/.png';
import logo from '../../img/logo.png';
import './Header.scss';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { userInfoType } from 'redux/actions/ActionTypes';
import { usePreventRefresh } from 'components/customhook/refresh/Refresh';
import { Button, Container, Form, InputGroup, Nav, Navbar } from 'react-bootstrap';

const size1 = {
    width: '30px',
    height: '30px'
};

const size2 = {
    width: '45px',
    height: '45px'
};

const Header: React.FC = () => {
    const nav = useNavigate();
    const isLogin = useSelector((state: any) => state.isLogin);
    const user = useSelector((state: { user: userInfoType }) => state.user);
    usePreventRefresh();

    useEffect(() => {
        console.log(user.nick);
    }, [isLogin]);
    return (
        <div>
            {/*   ( ) */}
            <Navbar bg="secondary" expand="lg" sticky='top'>
                <Container>
                    {/*  */}
                    <Navbar.Brand href={process.env.PUBLIC_URL}>  {/*     */}
                        <img
                            className='cursor'  //     
                            src={logo}  //   
                            alt=""
                            style={{ width: 225, height: 48, marginRight: 10}}  //   
                        />
                    </Navbar.Brand>

                    {/*        */}
                    <Navbar.Toggle aria-controls="navbar-nav" />

                    {/*   (   ) */}
                    <Navbar.Collapse id="navbar-nav">
                        <Nav className="me-auto">
                            {/*  */}
                            <InputGroup className="search-group">  {/*   */}
                                <Form.Control
                                    type="text"
                                    placeholder=""  //    
                                    aria-label="Search"  //    
                                    name="searchBox"  //    
                                    className='searchBox'
                                />
                                {/*   ( ) */}
                                <Button variant="outline-primary">
                                    <img src={SearchOk} alt="search icon" style={size1} />  {/*   */}
                                </Button>
                            </InputGroup>
                        </Nav>

                        {/*      */}
                        <Nav>
                            {/*     */}
                            <Nav.Item>
                                {user.nick && (  //    
                                    <Nav.Link disabled >{`${user.nick}  `}</Nav.Link>
                                )}
                            </Nav.Item>

                            {/*  ,      */}
                            <Nav.Item>
                                <Nav.Link 
                                className='cursor'
                                onClick={() => nav("/login")}>
                                    <img src={Profile} alt="" style={size2} />  {/*   */}
                                </Nav.Link>
                            </Nav.Item>

                            {/*   */}
                            {/* <Nav.Item>
                                <Nav.Link>
                                    <img src={MenuBtn} alt="" style={size} /> 
                                </Nav.Link>
                            </Nav.Item> */}
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            {/*    (, ) */}
            <div className='menuContainer'>
                {/* ''     */}
                <span className='cursor standard' onClick={() => nav('/')}></span>
                {/* ''      */}
                <span className='cursor standRank' onClick={() => nav('/rank')}></span>
            </div>
        </div>
    );
};

export default Header;
